:root {
  --mc-red: #d21404;
  --mc-dark-red: #a50f03;
  --mc-yellow: #fdd835;
  --mc-gold: #ffcc00;
  --mc-cream: #fff5e6;
  --mc-card: #fff8dc;
  --mc-text: #2b2b2b;
}

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  background: var(--mc-cream);
}

.App {
  padding: 1.5rem;
  text-align: center;
  background : var(--mc-cream);
}

h1 {
  color: var(--mc-red);
  font-size: clamp(2rem, 3vw, 2.8rem);
  letter-spacing: 0.05em;
  text-shadow:
    0.08rem 0.08rem 0 var(--mc-yellow),
    0.16rem 0.16rem 0 rgba(0,0,0,0.15);
  margin-bottom: rem;
}

.sandwich-card {
  border: 0.18rem solid var(--mc-red);
  border-radius: 1.2rem;
  padding: 1.2rem;
  margin: 1rem auto;
  max-width: 26rem;
  width: 90%;
  background: var(--mc-card);
  box-shadow: 0 0.4rem 1rem rgba(0,0,0,0.15);
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.sandwich-card:hover {
  transform: translateY(-0.3rem) scale(1.02);
  box-shadow: 0 0.6rem 1.4rem rgba(0,0,0,0.25);
}

.sandwich-card h3 {
  background: var(--mc-cream);
  color: var(--mc-red);
  font-size: 1.4rem;
  margin-bottom: 0.3rem;
  margin-top: 0;
  border:0.1rem solid var(--mc-text);
  border-radius: 0.5rem;
  padding: 0.2rem 0.5rem;
}

.sandwich-card p {
  font-size: 1rem;
  line-height: 1.5;
  margin-top: 0.5rem;
  margin-bottom: 0;
}

.sandwich-card ol {
  text-align: left;
  padding-left: 1.2rem;
}

.button {
  background: linear-gradient(
    to bottom,
    var(--mc-yellow),
    var(--mc-gold)
  );
  border: none;
  padding: 0.9rem 1.8rem;
  margin-top: 1rem;
  border-radius: 3rem;
  font-weight: 900;
  font-size: 1rem;
  color: var(--mc-red);
  cursor: pointer;
  letter-spacing: 0.04em;

  box-shadow:
    0 0.25rem 0 var(--mc-dark-red),
    0 0.4rem 0.6rem rgba(0,0,0,0.2);

  transition: all 0.15s ease;
}

.button:hover {
  transform: translateY(-0.1rem);
  box-shadow:
    0 0.35rem 0 var(--mc-dark-red),
    0 0.6rem 1rem rgba(0,0,0,0.25);
}

.button:active {
  transform: translateY(0.25rem);
  box-shadow:
    0 0.1rem 0 var(--mc-dark-red),
    0 0.2rem 0.4rem rgba(0,0,0,0.2);
}

.menu {
  display: flex;
  justify-content: center;
  flex-direction:column;
  flex-wrap: wrap;
  gap: 1.2rem;
  margin : 8rem auto;
}

.menu button {
  background-color: var(--mc-red);
  color: white;
  border: none;
  padding: 1.2rem 2.4rem;
  font-size: 1.1rem;
  font-weight: bold;
  border-radius: 1rem;
  cursor: pointer;

  box-shadow: 0 0.35rem 0 #7f0c02;
  transition: all 0.2s ease;
}

.menu button:hover {
  background-color: #e21b0a;
  transform: translateY(-0.15rem);
}

.menu button:active {
  transform: translateY(0.3rem);
  box-shadow: 0 0.1rem 0 #7f0c02;
}

.cooking {
  display : flex;
  position : relative;
  margin-top: 0.5rem;
  margin-left : 0.5rem;
  font-weight: 800;
  color: var(--mc-text);
  /* text-decoration: underline; */
  text-decoration-color: var(--mc-text);
  text-decoration-thickness: 0.25rem;
  text-underline-offset: 0.3rem;
  border-radius : 0.5rem;
  
}


